<script>
import ClickToEdit from "./ClickToEdit.vue";
export default {
  name: "AppTableArbitratorItem",
  data() {
    return {
      edit: false,
      newScore: {},
    };
  },
  props: {
    item_sportsmen: {
      type: Object,
      default() {
        return {};
      },
    },
    idx: {
      type: Number,
      default() {
        return {};
      },
    },
  },
  components: {
    ClickToEdit,
  },
  computed: {
    maxScoreExe() {
      return Math.max.apply(null, Object.values(this.item_sportsmen.execution));
    },
    minScoreExe() {
      return Math.min.apply(null, Object.values(this.item_sportsmen.execution));
    },
    maxScoreArt() {
      return Math.max.apply(null, Object.values(this.item_sportsmen.artisty));
    },
    minScoreArt() {
      return Math.min.apply(null, Object.values(this.item_sportsmen.artisty));
    },
    averageExe() {
      return +(
        (Object.values(this.item_sportsmen.execution).reduce((a, b) => a + b) -
          this.minScoreExe -
          this.maxScoreExe) /
        2
      ).toFixed(3);
    },

    averageArt() {
      return +(
        (Object.values(this.item_sportsmen.artisty).reduce((a, b) => a + b) -
          this.minScoreArt -
          this.maxScoreArt) /
        2
      ).toFixed(3);
    },
    averageDif() {
      return this.item_sportsmen.difficulty.dif1 / 2;
    },
    dedElem() {
      return this.item_sportsmen.deduction.element / 2;
    },
    averageDed() {
      return +(
        Object.values(this.item_sportsmen.deduction).reduce((a, b) => a + b) -
        this.dedElem
      ).toFixed(2);
    },
    overallScore() {
      return +(
        this.averageExe +
        this.averageArt +
        this.averageDif -
        this.averageDed
      ).toFixed(3);
    },
  },
  methods: {
    closeSportsmen() {
      this.$emit("closeScore", {
        sportsmen: this.item_sportsmen.name,
        overallScore: this.overallScore,
      });
    },
    closeSportsmenEdit() {
      this.$emit("closeSportsmenEdit", {
        sportsmen: this.item_sportsmen.name,
        newScore: this.newScore,
      });
      this.edit = !this.edit;
    },
    input(value) {
      this.newScore = value;
      console.log("ЭТО ЯЯЯЯ", this.newScore);
    },
    editItem() {
      this.edit = !this.edit;
    },

    putAxios() {
      console.log("Меняем");
      const forms = document.forms;
      const formData = new FormData(forms[1]);
      this.newcritical = [];
      for (let [name, value] of formData) {
        this.newcritical = { ...this.newcritical, [name]: value };
        console.log(this.newcritical);
        console.log(`${name}: ${value}`);
      }
      // this.$store
      //   .dispatch(actionTypes.putCompanieItem, {
      //     amount: this.storage_item.amount,
      //     avg_consumption: this.storage_item.avg_consumption,
      //     critical: Number(this.newcritical.critical),
      //     days_left: this.storage_item.days_left,
      //     for_order: this.storage_item.for_order,
      //     // id: this.storage_item.id,
      //     company_id: this.storage_item.company_id,
      //     product_title: this.storage_item.product_title,
      //     unit: this.storage_item.unit,
      //   })
      //   .then(() => {
      //     console.log("Put");
      //     this.$emit("onSelect");
      //     this.edit = !this.edit;
      //   })
      //   .catch((er) => {
      //     console.log("Ошибка", er);
      //   });
    },
  },
};
</script>

