<template>
  <div class="xlsx-download">
    <h3 class="xlsx-download__title">Загрузить результаты в формате XLSX</h3>
    <input type="file" ref="fileupload" @change="onChange" />
    <div class="add-resuld">
      <div class="select-block__item-block">
        <label for="standard-select">Выберите номинацию</label>
        <div class="select">
          <select required id="standard-select" v-model="nominate">
            <option value="ИЖ">ИЖ</option>
            <option value="ИМ">ИМ</option>
            <option value="СП">СП</option>
            <option value="ТР">ТР</option>
            <option value="ГР">ГР</option>
            <option value="ТГ">ТГ</option>
            <option value="ГП">ГП</option>
          </select>
          <span class="focus"></span>
        </div>
      </div>
      <div class="select-block__item-block">
        <label for="standard-select">Выберите возрастную категорию</label>
        <div class="select">
          <select required id="standard-select" v-model="age">
            <option value="6-8">6-8</option>
            <option value="2014">2014 год</option>
            <option value="2015">2015 год</option>
            <option value="2016">2016 год</option>
            <option value="9-11">9-11</option>
            <option value="2011">2011 год</option>
            <option value="2012">2012 год</option>
            <option value="2013">2013 год</option>
            <option value="12-14">12-14</option>
            <option value="2008">2008 год</option>
            <option value="2009">2009 год</option>
            <option value="2010">2010 год</option>
            <option value="15-17">15-17</option>
            <option value="2005">2005 год</option>
            <option value="2006">2006 год</option>
            <option value="2007">2007 год</option>
            <option value="18+">18+</option>
          </select>
          <span class="focus"></span>
        </div>
      </div>
    </div>
    <div class="xlsx-download__input">
      <input
        type="submit"
        value="Загрузить результаты"
        class="button btn-primary"
        @click="onSubmitProtocol()"
        :disabled="!this.file"
      />
    </div>
    <xlsx-read :file="file" v-if="this.file !== null">
      <xlsx-table />
    </xlsx-read>
  </div>
</template>

<script>
import { XlsxRead, XlsxTable } from "vue-xlsx";
// import axios from "axios";
import { actionTypes } from "@/store/modules/board";

export default {
  components: {
    XlsxRead,
    XlsxTable,
  },
  data() {
    return {
      file: null,
      nominate: "",
      age: "",
    };
  },
  methods: {
    onChange(event) {
      this.file = event.target.files ? event.target.files[0] : null;
    },
    onSubmitProtocol() {
      let formData = new FormData();
      formData.append("file", this.file);
      formData.append("age", this.age);
      formData.append("nominate", this.nominate);

      this.$store
        .dispatch(actionTypes.postPlayers, formData)
        .then(() => {
          (this.file = null),
            (this.nominate = ""),
            (this.age = ""),
            (this.$refs.fileupload.value = null);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>
