import Vue from "vue";
import VueRouter from "vue-router";
import Board from "../views/Board.vue";
import Info from "../views/InfoInput";
import Index from "../views/Index";
import AddResult from "../views/AddResult";
import Login from "../views/Login";
import ScoreEA from "../views/ScoreEA";
import ScoreDif from "../views/ScoreDif";
import Registration from "../views/Registration";
import Arbitrator from "../views/Arbitrator";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Index",
    component: Index,
    meta: {
      Header: true,
      Footer: true,
    },
  },
  {
    path: "/login",
    name: "Login",
    component: Login,
    meta: {
      Header: false,
      Footer: true,
    },
  },
  {
    path: "/registration",
    name: "Registration",
    component: Registration,
    meta: {
      Header: true,
      Footer: true,
    },
  },
  {
    path: "/board",
    name: "Board",
    component: Board,
    meta: {
      Header: false,
      Footer: false,
    },
  },
  {
    path: "/info",
    name: "Info",
    component: Info,
    meta: {
      Header: true,
      Footer: true,
    },
  },
  {
    path: "/addResult",
    name: "AddResult",
    component: AddResult,
    meta: {
      Header: true,
      Footer: true,
    },
  },
  {
    path: "/score",
    name: "ScoreEA",
    component: ScoreEA,
    meta: {
      Header: true,
      Footer: true,
    },
  },
  {
    path: "/scoreDif",
    name: "ScoreDif",
    component: ScoreDif,
    meta: {
      Header: true,
      Footer: true,
    },
  },
  {
    path: "/arbitr",
    name: "Arbitr",
    component: Arbitrator,
    meta: {
      Header: true,
      Footer: true,
    },
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
