<template>
  <td>
    <input
      type="text"
      v-if="edit"
      :value="valueLocal"
      @keyup.enter="
        valueLocal = $event.target.value;
        edit = false;

        $emit('input', { score: Number(valueLocal), judge: numberJuj });
      "
      v-focus=""
      style="width: 31px; height: 29px"
    />
    <p v-else @click="edit = true">
      {{ valueLocal }}
    </p>
  </td>
  <!-- @blur="
        valueLocal = $event.target.value;
        edit = false;
        $emit('input', valueLocal);
      " -->
</template>

<script>
export default {
  props: ["value", "numberJuj"],

  data() {
    return {
      edit: false,
      valueLocal: this.value,
      juj: this.numberJuj,
    };
  },

  watch: {
    value: function () {
      this.valueLocal = this.value;
      this.juj = this.numberJuj;
    },
  },
  methods: {
    onChange() {
      let newScore = {
        score: Number(this.valueLocal),
        juj: this.juj,
      };
      this.$emit("input", newScore);
    },
  },

  directives: {
    focus: {
      inserted(el) {
        el.focus();
      },
    },
  },
};
</script>
