import axios from "@/api/axios";

const authorizationAxios = axios.create({
  baseURL: "http://109.111.185.150:9021",
});

const login = (credentials) => {
  return authorizationAxios.post(`/login`, credentials, {
    useCredentails: true,
  });
};
const registration = (credentials) => {
  return authorizationAxios.post(`/registration `, credentials, {
    useCredentails: true,
  });
};
export default {
  login,
  registration,
};
